@font-face {
    font-family: 'nuclear-icons';
    src:  url('fonts/nuclear-icons.eot?hwfkcb');
    src:  url('fonts/nuclear-icons.eot?hwfkcb#iefix') format('embedded-opentype'),
    url('fonts/nuclear-icons.ttf?hwfkcb') format('truetype'),
    url('fonts/nuclear-icons.woff?hwfkcb') format('woff'),
    url('fonts/nuclear-icons.svg?hwfkcb#nuclear-icons') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'nuclear-icons' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

