.form-group {
  margin: 0.5em 0 1.5em; }
  .form-group::after {
    clear: both;
    content: "";
    display: table; }
  .form-group--inline {
    display: inline-block;
    min-width: 24%; }
  .form-group__label {
    text-align: left;
    font-size: 0.875em;
    font-weight: 700;
    font-family: "Lato", Helvetica, sans-serif;
    color: #95A5A6;
    -webkit-transition: color 250ms;
    -moz-transition: color 250ms;
    transition: color 250ms;
    padding-left: 1em;
    position: relative;
    display: block;
    line-height: 1.25em; }
    .form-group__label:before {
      content: '';
      width: 0.75em;
      height: 1px;
      position: absolute;
      bottom: 0.25em;
      border-bottom: 1px solid #95A5A6;
      left: 0;
      -webkit-transition: border-bottom-color 250ms;
      -moz-transition: border-bottom-color 250ms;
      transition: border-bottom-color 250ms; }
    .form-group__label--focus {
      color: #F1C40F; }
      .form-group__label--focus:before {
        border-bottom-color: #F1C40F; }
    .form-group__label--error {
      color: #E74C3C; }
      .form-group__label--error:before {
        border-bottom-color: #E74C3C; }
  .form-group input, .form-group textarea {
    border-bottom: 1px solid #95A5A6;
    -webkit-transition: border-bottom 250ms;
    -moz-transition: border-bottom 250ms;
    transition: border-bottom 250ms; }
    .form-group input:focus, .form-group textarea:focus {
      border-bottom: 1px solid #F1C40F; }
  .form-group textarea {
    max-width: 28em;
    min-height: 6em;
    resize: vertical; }
  .form-group__select {
    position: relative;
    border-bottom: 1px solid #95A5A6; }
    .form-group__select i {
      position: absolute;
      top: 0.625em;
      right: 0.375em;
      color: #95A5A6;
      font-size: 0.875em;
      -webkit-transition: color 250ms;
      -moz-transition: color 250ms;
      transition: color 250ms; }
    .form-group__select select {
      height: 2em;
      position: relative;
      width: 100%;
      z-index: 1;
      cursor: pointer;
      background: transparent;
      -webkit-appearance: none;
      border: 0;
      box-shadow: none;
      outline: none;
      padding-right: 1.5em;
      font-size: 1em;
      color: #5F696A; }
  .form-group__checkbox span {
    color: #E74C3C;
    -webkit-transition: color 250ms;
    -moz-transition: color 250ms;
    transition: color 250ms; }
    .form-group__checkbox span .icon-confirm {
      display: none; }
  .form-group__checkbox input {
    display: none; }
    .form-group__checkbox input:checked + span {
      color: #F1C40F; }
      .form-group__checkbox input:checked + span .icon-confirm {
        display: inline-block; }
      .form-group__checkbox input:checked + span .icon-cancel {
        display: none; }
  .form-group--error input, .form-group--error textarea {
    border-bottom-color: #E74C3C; }
  .form-group--inverted input:not([type="checkbox"]), .form-group--inverted textarea, .form-group--inverted select {
    color: #FFFFFF; }

